.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body{
  margin: 0;
  font-family: 'Space Mono', monospace;
}

img {
   border:1px solid #021a40;
}
.grow { -webkit-transition: all .2s ease-in-out; -o-transition: all .2s ease-in-out; transition: all .2s ease-in-out; }

.grow:hover { -webkit-transform: scale(1.1); -ms-transform: scale(1.1); transform: scale(1.1); }

.wrapper{
  min-height: 100vh;
  background: #ccc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.header, .footer{
  height: 50px;
  background: #666;
  color: #fff;
}
.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  background: #999;
  color: #000;
}
.columns{
  display: -ms-flexbox;
  display: flex;
  -ms-flex:1 1;
      flex:1 1;
}
.main{
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-order: 2;
      order: 2;
  background: #BB8FCE;;
  overflow-y: scroll;
}
.sidebar-first{
  width: 15%;
  overflow-y: scroll;
  background: #A569BD  ;
  -ms-flex-order: 1;
      order: 1;
}
.sidebar-second{
  width: 15%;
  -ms-flex-order: 3;
      order: 3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow-x: scroll;
  -ms-flex-align: center;
      align-items: center;
  background: #A569BD  ;
}

form{
	width:500px;
	margin:0 auto;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}

.searchContainer{
  vertical-align: middle;
  text-align: center;
  white-space: nowrap;
  padding-top: 150px;
}


  .title {
    font-size: 2em;
    font-style: italic;
  }


.searchContainer input{
  width: 300px;
  height: 50px;
  background: #A569BD;
  border-width: 1px;
  border-color: black;
  font-size: 10pt;
  opacity: .4;
  color: #000000;
  padding-left: 45px;
  border-radius: 5px;
  display: block;
  margin : 0 auto;
  -webkit-transition: background .55s ease;
-o-transition: background .55s ease;
transition: background .55s ease;
}

.searchContainer input:hover, .searchContainer input:focus, .searchContainer input:active{
    outline:none;
    background: #ffffff;
  }



